#post_container {
  width: 58%;
  margin: 0 auto;
  /* background: rgb(146 151 179 / 13%); */
  background: rgb(249 250 251 / 55%);
  /* background-image: url(../../assets/images/post_background.jpg);
  background-size: cover;
  background-position: center; */
  /* border-radius: 14px; */
  border-radius: 1rem;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  font-size: 15px;
  font-weight: 500;
  /* padding: 2em; */
}
.window_header {
  width: 100%;
  height: 46px;
  display: flex;
  flex-shrink: 0;
  padding: 0 1.2rem;
  align-items: center;
  white-space: nowrap;
  border-bottom: 1px solid rgba(113, 119, 114, 0.25);
  /* flex-direction: row-reverse; */
}
.circle {
  width: 15px;
  height: 15px;
  flex-shrink: 0;
  border-radius: 50%;
  margin-right: 9px;
  margin-bottom: -6px;
}
.circle1 {
  background-color: #f85850;
  cursor: url('../../assets/cursor/pointer.cur'), auto !important;
}
.circle2 {
  background-color: #f7cc4a;
}
.circle3 {
  background-color: #5fd865;
}
.post_content {
  padding: 10px 1.2rem;
  color: #282741;
  font-size: 1.05rem;
  line-height: 1.8;
}
.post_content > h1 {
  text-align: center;
  line-height: 1;
}
.post_content > p {
  text-indent: 2em;
}
.post_content > img {
  display: block;
  margin: 0 auto;
}
.post_content code {
  padding: 0.2em 0.4em;
  margin: 0;
  font-size: 90%;
  background-color: #ffbe53;
  border-radius: 6px;
  font-family: SFMono-Regular, SF Mono, Menlo, Consolas, Liberation Mono, monospace;
  /* font-style: italic; */
  margin-right: 5px;
  font-weight: bold;
}
.slide-enter {
  opacity: 0;
  transform: scale(0.9);
}
.slide-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 400ms, transform 400ms;
}
.slide-exit {
  opacity: 1;
}
.slide-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 400ms, transform 400ms;
}
@keyframes JUMP {
  0% {
    transform: translateY(3px);
  }
  50% {
    transform: translateY(-9px);
  }
  100% {
    transform: translateY(3px);
  }
}
.jump {
  animation: JUMP 400ms 2 cubic-bezier(0.4, 0.01, 0.6, 0.9);
}
