.header {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 200px;
  text-align: center;
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
  color: #eee;
  margin-bottom: -6px;
}
.header > h1 {
  margin-top: 0;
}
.author {
  display: inline-block;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  background: url(../../assets/images/one-piece.jpg) center no-repeat;
  background-size: cover;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
  margin-bottom: 8px;
}
.meta {
  font-size: 0.9em;
  font-style: italic;
}
