@font-face {
  font-family: 'Droid Serif';
  src: url(./assets/fonts/DroidSerif-Regular.ttf);
}
@font-face {
  font-family: 'Abril Fatface';
  src: url(./assets/fonts/AbrilFatface-Regular.otf);
}
:root {
  --scrollbar-bg: rgb(1 2 3 / 40%);
}
.light-mode {
  --scrollbar-bg: rgb(255 253 253 / 57%);
}
body {
  font-family: 微软雅黑, 'Droid Serif', Arial, serif;
  cursor: url('./assets/cursor/default.cur'), auto !important;
  overflow-x: hidden;
}
html,
body {
  height: 100%;
  background: transparent;
}
/* img, */
a {
  cursor: url('./assets/cursor/pointer.cur'), auto !important;
}
::-webkit-scrollbar {
  width: 6px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: var(--scrollbar-bg);
  border-radius: 10px;
}
